	module.exports = {"data":{"wordpressPost":{"title":"Research priorities for Parkinson&#8217;s, aphasia rehabilitation and urban planning: Most read articles in July","content":"<p>A number of new papers are included in July&#8217;s Top 10 Most Read, including a realist policy analysis research programme by Harris <em>et al</em> which aims to observe the l<span class=\"search-term-highlight\">a</span>nd-use pl<span class=\"search-term-highlight\">a</span>nn<span class=\"search-term-highlight\">in</span>g system <span class=\"search-term-highlight\">in</span> New South W<span class=\"search-term-highlight\">a</span>les, <span class=\"search-term-highlight\">A</span>ustr<span class=\"search-term-highlight\">a</span>li<span class=\"search-term-highlight\">a</span>, and how it <span class=\"search-term-highlight\">in</span>corpor<span class=\"search-term-highlight\">a</span>tes <span class=\"search-term-highlight\">health</span> <span class=\"search-term-highlight\">a</span>nd <span class=\"search-term-highlight\">health</span> equity <span class=\"search-term-highlight\">a</span>t multiple levels. Power <em>et al</em>&#8216;s article takes a look at <span class=\"search-term-highlight\">best</span> <span class=\"search-term-highlight\">practice</span> <span class=\"search-term-highlight\">statements</span> for use in post-stroke <span class=\"search-term-highlight\">aphasia</span> <span class=\"search-term-highlight\">rehabilitation</span>. Deane <em>et al</em>&#8216;s paper on <span class=\"search-term-highlight\">identifying</span> and prioritising evidential uncertainties that impact on everyday <span class=\"search-term-highlight\">management</span> <span class=\"search-term-highlight\">of</span> <span class=\"search-term-highlight\">Parkinson&#8217;s</span> <span class=\"search-term-highlight\">disease </span>and O&#8217;Keeffe <em>et al</em>&#8216;s paper on predictors of alcohol use during pregnancy were also popular papers this month.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Branas <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e005619.full\">The impact of economic austerity and prosperity events on suicide in Greece: a 30-year interrupted time-series analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Iparraguirre</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/7/e007684.full\">Socioeconomic determinants of risk of harmful alcohol drinking among people aged 50 or over in England</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Power <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/7/e007641.full\">Development and validation of Australian aphasia rehabilitation best practice statements using the RAND/UCLA appropriateness method</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">O&#8217;Keeffe <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/7/e006323.full\">Prevalence and predictors of alcohol use during pregnancy: findings from international multicentre cohort studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Parand <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/9/e005055.full\">The role of hospital managers in quality and patient safety: a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Rachiotis<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/3/e007295.full\">What has happened to suicides during the Greek economic crisis? Findings from an ecological study of suicides and their determinants (2003-2012)</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Whiting <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/7/e008155.full\">How well do health professionals interpret diagnostic information? A systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Marston <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/8/e004996.full\">Anal heterosex among young people and implications for health promotion: a qualitative study in the UK</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Deane<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006434.full\">Priority setting partnership to identify the top 10 research priorities for the management of Parkinson&#8217;s disease</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Harris <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/7/e008822.full\">&#8216;Including health in systems responsible for urban planning&#8217;: a realist policy analysis research programme</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"3fc4b5a3-65bd-59f5-9883-77772799f3b2"}}

